.resume h1, p{
    color: #000;
}

.date-pill {
    border-radius: 15px;
    background-color: #3586ff;
    color: #FFF;
    font-weight: 800;
    padding: 5px 8px;
    font-size: 12px;
}

.date-pill i {
    color: #fff;
}

.company-title{
    color: #000;
    font-weight: bold;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
}

.work-card {
    // min-height: 440px;
}

.blue-icon {
    color: #3586ff;
}

.skill-block{
    background-color: #a4a3a3;
    width: 55px; 
    height: 7px;
    border-radius: 5px;
    margin-right: 3px;
}

.skill-block.filled{
    background-color: #3586ff;
}

.skill-name{
    color: #4f4e4e;
    font-size: 15px;
    font-weight: 700;
}