.contact-me {
background-color: #FAFBFD;
}

.contact-me-img {
    width: 450px;
    height: auto;
    animation: moveUpDown 5s infinite linear;
}

@media (max-width: 600px) {
    .contact-me-img{ 
        width: 300px;
    }
}

@keyframes moveUpDown {
    0% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(-15px);
    }

    100% {
        transform: translateY(0px);
    }
}